import React, {useEffect, useState} from 'react';
import {connect} from 'react-redux';
import { getNumbers } from '../actions/getAction';
import {Link} from  'react-router-dom';

const Header = (props)=>{
  const [showsearch , setshowsearch] = useState(false);

  useEffect(() => {  
    getNumbers();
  }, [])

     return(      
        <header className="header">
                <nav className="navbar navbar-light justify-content-between primaryColor">
                    <a className="navbar-brand" href="/"><i className="fas fa-sun"></i></a>
                    <div className="shoping-icons">
                     
                    <form className="form-inline" onSubmit={e=>e.preventDefault()}>
                      <input className={ showsearch ? "form-control mr-sm-2 switchOn" : "form-control mr-sm-2 switchOff"} type="search" placeholder="Search" aria-label="Search"/>
                      <button className="btn btn-outline-light my-2 my-sm-0" type="submit" onClick={()=>{setshowsearch(!showsearch)}}><i className="fas fa-search"></i></button>
                    </form>     
                    <Link exact to="/shop"><button className="mx-5 btn btn-info shopnow-btn">Shop Now</button></Link>
                    <div className="cart-icon">
                    <Link to="/cart"><button className="btn btn-outline-light my-2 my-sm-0" type="button" to={'/cart'}><div className="cartCount">{props.basketProps.basketNumbers}</div><i className="fas fa-shopping-cart"></i></button></Link>
                    </div>
                    </div>
                </nav>

            {/* <nav className="navbar navbar-expand-lg navbar-light primaryColor">
                      <button className="navbar-toggler order-1" type="button" data-toggle="collapse" data-target="#navbarTogglerDemo03" aria-controls="navbarTogglerDemo03" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                      </button>
                      <a className="navbar-brand" href="https://getbootstrap.com/docs/4.0/components/navbar/"><i className="fas fa-sun"></i></a>

                      <div className="collapse navbar-collapse" id="navbarTogglerDemo03">
                        <ul className="navbar-nav mr-auto mt-2 mt-lg-0">
                          <li className="nav-item active">
                            <a className="nav-link" href="#">Home <span className="sr-only">(current)</span></a>
                          </li>
                          <li className="nav-item">
                            <a className="nav-link" href="#">Link</a>
                          </li>
                          <li className="nav-item">
                            <a className="nav-link disabled" href="#">Disabled</a>
                          </li>
                        </ul>
                        <div className="shoping-icons">
                        <form className="form-inline my-2 my-lg-0" onSubmit={e=>e.preventDefault()}>
                        <input className={props.searchEff ? "form-control mr-sm-2 switchOn" : "form-control mr-sm-2 switchOff"} type="search" placeholder="Search" aria-label="Search"/>
                        <button className="btn btn-outline-light my-2 my-sm-0" type="submit"  onClick={props.clicked}><i className="fas fa-search"></i></button>
                        </form>
                        <div className="cart-icon">
                            <button className="btn btn-outline-light my-2 my-sm-0" type="button"><i className="fas fa-shopping-cart"></i></button></div>               
                        </div>
                        </div>
            </nav> */}
        </header>
    );
}

const mapStateToProps = state => ({
basketProps: state.basketState
})

export default connect( mapStateToProps, {getNumbers})(Header);