import React from 'react';

const SortSection = (props)=>{
    return(
        <section className="sort">
            <ul>
                 <li>Sort</li>
                 <li><a href="#" onClick={props.sortItem}>Price -- High Low</a></li>
                 <li><a href="" onClick={props.sortItemasc}>Price -- Low High</a></li>
                 <li><a href="" onClick={props.discount}>Discount</a></li>
            </ul>
        </section>
    );
}

export default SortSection;