import React from 'react';
import ShopingList from './ShopingList';
import SortSection from './SortSection';

const ShopingSection = (props)=>{
    return(
        <section className="container">
            <div className="row">
                <div className="col-md-12 d-none d-sm-block">
                    <SortSection sortItem={props.sortItem} sortItemasc={props.sortItemasc} discount= {props.discount}/>
                </div>
            </div>
            <div className="row">
                <div className="col-md-12">
                    <ShopingList desorted={props.desorted} data1={props.data} sortEvent={props.sortEvent} cart={props.cart} iii={props.iii} addToCart={props.addToCart} sortItemasc={props.sortItemasc}/>
                </div>
            </div>
        </section>
    );
}

export default ShopingSection;