import React, { Fragment } from 'react';
import { connect } from 'react-redux';
import { GET_NUMBERS_BASKET } from '../actions/types';
import { productQuantity, clearProduct } from '../actions/productQuantity';
import I1 from '../assets/i1.jpg';
import I2 from '../assets/i2.jpg';
import I3 from '../assets/i3.jpg';
import {Link} from 'react-router-dom';
function CartPage({basketProps, productQuantity, clearProduct}) {

    let productsInCart = []; 

    Object.keys(basketProps.products).forEach((item)=>{
        if(basketProps.products[item].inCart){
            productsInCart.push(basketProps.products[item])
        }
    })

    // const productImages = [ I1, I2, I3, I2, I1, I1, I1, I1, I1, I1, I1, I1, I1, I1, I1, I1, I1, I1, I1, I1, I1, I1, I1, I1, ]

    const productImages = (product) =>{
        if(product.tagName === 'item1'){
            return I1
        }else if(product.tagName === 'item2'){
            return I2;
        }else if(product.tagName === 'item3'){
            return I3;
        }else if(product.tagName === 'item4'){
            return I2;
        }else if(product.tagName === 'item5'){
            return I1;
        }else if(product.tagName === 'item6'){
            return I3;
        }else if(product.tagName === 'item7'){
            return I2;
        }else if(product.tagName === 'item8'){
            return I1;
        }else if(product.tagName === 'item9'){
            return I2;
        }else if(product.tagName === 'item10'){
            return I3;
        }else if(product.tagName === 'item11'){
            return I1;
        }else if(product.tagName === 'item12'){
            return I2;
        }else if(product.tagName === 'item13'){
            return I3;
        }else if(product.tagName === 'item14'){
            return I2;
        }else if(product.tagName === 'item15'){
            return I3;
        }else if(product.tagName === 'item16'){
            return I1;
        }else if(product.tagName === 'item17'){
            return I2;
        }else if(product.tagName === 'item18'){
            return I3;
        }else if(product.tagName === 'item19'){
            return I2;
        }else if(product.tagName === 'item20'){
            return I1;
        }else if(product.tagName === 'item21'){
            return I2;
        }else if(product.tagName === 'item22'){
            return I3;
        }else if(product.tagName === 'item23'){
            return I1;
        }else if(product.tagName === 'item24'){
            return I2;
        }else if(product.tagName === 'item25'){
            return I3;
        }
    }

    productsInCart= productsInCart.map((product, index)=>{
        return (
            <Fragment key={index}>
                <div className="container">
                        <div className="row">
                                <div className="col-md-12">
                                    <div className="row">
                                    <div className="col-md-12 bdr-r" id="ds">




    <div className="row mobile-v">
        <div className="col-md-5 mobile-v-img">
            <div className="row">
                    <div className="col-md-4 mbl-pdt-img" >
                            <img alt={product.title} src={productImages(product)} className="img-fluid"/>
                    </div>
                    <div className="col-md-5 p-0 mbl-pdt-price">
                            <h3 style={{marginTop:'2rem'}}>{product.title}</h3>
                            <p className="card-text"><span className="price">â‚¹{product.price}</span> <span className="realPrice">{product.withOutOffer}</span> <span className="offerprice1">{product.offer}% off</span></p>

                            <div className="row d-block d-sm-none">
                            <div className="col-sm-12 ctr mobile-v-price">
                            <div className="row">
                                <div className="col-sm-3">
                                <button className="minus" onClick={()=>productQuantity('decrease', product.title)}>-</button>
                                </div>
                                <div className="col-sm-4 p-0 mob-qntity">
                                {product.numbers}
                                </div>
                                <div className="col-sm-4">
                                <button className="plus" onClick={()=>productQuantity('increase', product.title)}>+</button>
                                </div>

                            </div>
<div></div>
                            <div className="row dsd">
                            <div className="col-md-4 ctr mobile-v-quantity">
                                     <button onClick={() => clearProduct(product.title)} className="btn btn-warning">Remove</button>
                                </div>
                            </div>
                
                    </div>
                            </div>
                    </div>


                    



            </div>
            
        </div>
        <div className="col-md-3 ctr mobile-v-price d-none d-sm-block">
            <div className="row">
                <div className="col-md-4">
                <button className="minus" onClick={()=>productQuantity('decrease', product.title)}>-</button>
                </div>
                <div className="col-md-4 p-0">
                {product.numbers}
                </div>
                <div className="col-md-4">
                <button className="plus" onClick={()=>productQuantity('increase', product.title)}>+</button>
                </div>
            </div>
                
        </div>
        <div className="col-md-4 ctr mobile-v-quantity d-none d-sm-block">
                <button onClick={() => clearProduct(product.title)} className="btn btn-danger">Remove</button>
        </div>
    </div>
</div>







                                    </div>
                                </div>
                               
                                
                        </div>
                </div>
               
     
            </Fragment>
        
        )
    })

    return <div className="container">
        <div className="row mb-2">
            <div className="col-md-12">
            { (productsInCart != '')? null : <div className="alert alert-warning" role="alert">
                   Shoping Cart is Empty
            </div>}
            </div>
        </div>

        <div className="row d-block d-sm-none">
            <div className="col-sm-12 text-center">                
            <Link exact to="/shop"><button class="mx-5 btn btn-info mx-auto">Shop Now</button></Link>
            </div>
        </div>

        <div className="row total-mb">     
            <div className="col-md-8 col-sm-12">
                {productsInCart} 
            </div>
            { (productsInCart != '')? <div className="col-md-4 col-sm-12 bdr-r totalCost ">
            <h2>Total Price:</h2><h1>{basketProps.cartCost}</h1>            
            </div> : null }
            
        </div>
        
    </div>

}
const mapStateToProps = state => ({    
    basketProps: state.basketState
})

export default connect(mapStateToProps, {productQuantity, clearProduct})(CartPage);